import React, { useState, useEffect } from "react"
import { PaystackButton } from "react-paystack"
import axios from "axios";
import { useAlert } from "react-alert";
import "./Paystack.css";

const Paystack = (params) => {
  const publicKey = 'pk_test_0be4d6378df1b00099a0ca8bb24ab1443ee17da9';
  const [amountInNGN, setAmountInNGN] = useState(0)
  const [exchangeRate, setExchangeRate] = useState(1);
  const alert = useAlert();

  useEffect(() => {
    const data2 = {
    currency: "USDTNGN",
    };
      axios
    .post("https://ozchest.com/getCryptoRate", data2)
    .then((response) => {
      if (response) {
        let erate = 1/response.data.cur;
        setExchangeRate(erate);
        setAmountInNGN(Number(params?.amount)/erate);
      }
    });
  },[]);

  useEffect(() => {
    setAmountInNGN(Number(params?.amount)/exchangeRate);
  },[params]);

  const handleUpdateBalance = (reference) => {
      const data = {
        user: localStorage.getItem("key"),
        amount: params.amount
      };
      axios.post("https://ozchest.com/addBalance", data).then((res) => {   
        if (res) {
            localStorage.setItem('balance', res.data.balance)
            alert.success('Balance added successfully');
            window.location.reload();
        } 
      });
    }

  const componentProps = {
    reference: (new Date()).getTime().toString(),
    email: params.email,
    amount: parseInt(amountInNGN)*100,
    publicKey: publicKey,
    text: "Refill",
    onSuccess: (reference) => handleUpdateBalance(reference),
    onClose: () => {
      console.log('exittt');
    }
  }

  return (
    <div className="refill-div">
        <PaystackButton {...componentProps} />
    </div>
  )
}

export default Paystack